@include font('helveticaneuecyr-bold', 'helveticaneuecyr-bold-webfont');
@include font('helveticaneuecyr-roman', 'helveticaneuecyr-roman-webfont');
@include font('helveticaneuecyr-light', 'helveticaneuecyr-light-webfont');
@include font('helveticaneuecyr-medium', 'helveticaneuecyr-medium-webfont');
@include font('helveticaneuecyr-thin', 'helveticaneuecyr-thin-webfont');

@mixin roman {
  font-family: 'helveticaneuecyr-roman', Arial, sans-serif; }
@mixin bold {
  font-family: 'helveticaneuecyr-bold', Arial, sans-serif; }
@mixin medium {
  font-family: 'helveticaneuecyr-medium', Arial, sans-serif; }
@mixin light {
  font-family: 'helveticaneuecyr-light', Arial, sans-serif; }
@mixin thin {
  font-family: 'helveticaneuecyr-thin', Arial, sans-serif; }


@mixin xl {
  @include r(1440) {
    @content; } }
@mixin lg {
  @include r(1199) {
    @content; } }
@mixin md {
  @include r(991) {
    @content; } }
@mixin sm {
  @include r(767) {
    @content; } }
@mixin xs {
  @include r(575) {
    @content; } }

@mixin lgmin {
  @include r(1200) {
    @content; } }
@mixin mdmin {
  @include r(992) {
    @content; } }
@mixin smmin {
  @include r(768) {
    @content; } }
@mixin xsmin {
  @include r(576) {
    @content; } }


body {
  background: #FFF;
  line-height: 1.4;
  font-size: 15px;
  @include light;
  color: $c-text;
  @include no-scroll;
  @include xs {
    font-size: 14px; } }

.out {
  overflow-x: hidden; }

img {
  max-width: 100%; }

strong {
  font-weight: 400;
  @include bold; }

.icon {
  display: inline-block; }

.btn {
  $this: &;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  background-color: $c-accent;
  @include light;
  @include inline-flex(center, center);
  padding: 13px 11px;
  position: relative;
  @include tr(background-color .3s);
  @include hover {
    background-color: #282822; }
  span {
    margin-right: 10px;
    padding-right: 10px;
    border-right: 1px solid  #fff;
    display: inline-flex; }
  .icon {
    fill: currentColor; }

  &--full {
    width: 100%; }
  &--link {
    background-color: transparent;
    color: $c-accent;
    font-size: 16px; }
  &--lg {
    padding-top: 17px;
    padding-bottom: 17px;
    @include sm {
      padding-top: 14px;
      padding-bottom: 14px; } }
  &--arrow {
    padding-right: 30px;
    @include before {
      display: inline-block;
      pointer-events: none;
      @include icon-arrow(12, 12, 1, #fff, -135);
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translate(0, -50%) rotate(-135deg); } }
  &--link {
    padding-left: 0;
    padding-right: 0; }
  &--combine {
    padding-right: 52px;
    padding-left: 15px;
    span {
      margin: 0;
      padding: 0;
      border: 0; }
    .icon {
      font-size: 16px; }
    .icon-heart {
      font-size: 20px; }
    #{$this}__title {
      @include inline-flex(space-between, center);
      width: 100%;
      @include tr(background-color .3s);
      .icon {
        margin-left: 15px; } }
    #{$this}__icon {
      @include inline-flex(center, center);
      background-color: #35383e;
      position: absolute;
      top: 0;
      right: 0;
      width: 42px;
      height: 100%;
      @include tr(background-color .3s); } }
  &--sm {
    padding: 8px;
    .icon {
      font-size: 21px; } }
  &--combine-double {
    @include hover {
      background-color: $c-accent; }
    #{$this}__title {
      @include hover {
        background-color: #282822; } }
    &#{$this}--sm {
      padding: 0;
      #{$this}__title {
        padding-right: 52px;
        padding-left: 15px;
        padding-top: 8px;
        padding-bottom: 8px;
        height: 100%; }
      #{$this}__icon {
        @include hover {
          background-color: $c-text-100; } } } } }

.btn-arrow {
  $this: &;
  color: #fff;
  @include light;
  font-size: 15px;
  @include inline-flex(flex-start, center);
  padding: 15px 20px;
  padding-right: 10px;
  background-color: #2c2c2c;
  @include hover {
    #{$this}__arrow {
      width: 40px; } }
  &__title {
    display: block;
    margin-right: 10px; }
  &__arrow {
    position: relative;
    width: 25px;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
    display: block;
    transition: transform .3s, width .3s;
    &::before {
      width: 100%;
      content: '';
      position: absolute;
      height: 1px;
      background-color: #fff;
      top: 50%;
      right: -1px;
      transform: translate(0, -50%);
      transition: width .3s; }
    &::after {
      display: inline-block;
      position: absolute;
      @include icon-arrow(8, 8, 1, #fff, 225);
      content: '';
      top: 50%;
      transform: translate(0, -50%) rotate(225deg) !important;
      right: 0; } } }

.price {
  @include medium;
  font-size: 30px;
  color: $c-accent;
  @include sm {
    font-size: 28px; }
  @include xs {
    font-size: 26px; } }

.check {
  color: $c-accent;
  @include light;
  font-size: 14px;
  @include inline-flex(flex-start, center);
  &__title {
    display: block;
    // vertical-align: middle
    margin-left: 5px; }
  &__icon {
    display: block;
    // vertical-align: middle
    border: 1px solid  $c-accent;
    height: 15px;
    width: 15px;
    position: relative;
    @include before {
      @include icon-arrow(8, 5, 1, $c-accent, -45);
      display: inline-block;
      position: absolute;
      top: 2px;
      left: 2px; } } }

.highlighted {
  display: inline-block;
  background-color: #d7fde4;
  padding-left: 2px;
  padding-right: 2px; }

.hr, hr {
  height: 1px;
  width: 100%;
  background-color: $c-border;
  color: $c-border;
  border: none;
  margin-top: 25px;
  margin-bottom: 25px; }

.list-link {
  display: block;
  font-size: 18px;
  @include light;
  color: #383838;
  border-bottom: 1px solid  $c-border;
  position: relative;
  padding-bottom: 5px;
  padding-right: 10px;
  @include tr(color .3s);
  @include text-overflow;
  @include md {
    font-size: 14px;
    margin-bottom: 3px;
    padding-bottom: 3px; }
  @include before {
    display: inline-block;
    @include icon-arrow(10, 10, 1, currentColor, -135);
    position: absolute;
    top: 50%;
    transform: translate(0, -50%) rotate(-135deg);
    right: 1px;
    @include md {
      @include icon-arrow(7, 7, 1, currentColor, -135);
      transform: translate(0, -50%) rotate(-135deg); } }
  @include hover {
    text-decoration: underline;
    color: $c-accent; }
  @include notlast {
    margin-bottom: 15px; } }

.has-bd-top {
  border-top: 1px solid  $c-border; }

.has-bd-bottom {
  border-bottom: 1px solid  $c-border; }
