.header {
  position: relative;
  // z-index: 15
  @include lg {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 45px;
    &::before {
      content: '';
      z-index: 15;
      background-color: $c-black;
      width: 100%;
      height: 45px;
      top: 0;
      left: 0;
      position: absolute; } }
  &.is-active {
    .header__inner {
      transform: translate(0, 0); } }
  &__inner {
    position: relative;
    transition: transform .3s;
    @include lg {
      position: absolute;
      z-index: 11;
      width: 100%;
      height: calc(100vh - 45px);
      top: 100%;
      left: 0;
      background-color: #fff;
      transform: translate(0, -100%);
      overflow: auto;
      background-color: #e6e6e6; } }
  &__burger {
    display: none;
    position: relative;
    z-index: 20;
    padding-right: 15px;
    @include lg {
      display: block; } }
  &__logo {
    position: absolute;
    z-index: 20;
    top: 0;
    left: 15px;
    @include lg {
      left: 0; } }
  &__logo-wrap {
    @include lg {
      position: absolute;
      top: 0;
      left: 0; } }
  &__top {
    background-color: $c-black;
    position: relative;
    .container {
      @include lg {
        max-width: none; } }
    @include lg {
      padding-top: 10px;
      padding-bottom: 10px;
      padding-left: 160px;
      padding-right: 0;
      [class*="col-"] {
        position: static; } }
    @include md {
      padding-left: 130px; }
    @include xs {
      padding-left: 15px;
      padding-top: 40px; } }
  &__top-right {
    @include flex(space-between);
    @include lg {
      display: none; } }
  &__top-right-mob {
    display: none;
    background-color: $c-black;
    @include lg {
      display: flex;
      position: absolute;
      top: 0;
      right: 60px;
      z-index: 20; }
    @include xs {
      top: 45px;
      right: 0; } }
  &__btn {
      flex-grow: 1;
      // +lg
      //   display: inline-block
      //   min-width: 185px
      @include xs {
        min-width: 0;
        margin-bottom: 0;
        .btn {
          font-size: 0;
          height: 100%;
          padding-left: 14px;
          padding-right: 14px;
          span {
            margin: 0;
            padding: 0;
            border: 0; }
          .icon {
            font-size: 16px; } } } }
  &__title {
    text-transform: uppercase;
    font-size: 14px;
    @include light;
    color: #fff;
    padding-top: 13px;
    padding-bottom: 13px; }

  &__bottom {
    background-color: #fff;
    .container {
      @include lg {
        max-width: none; } }

    @include lg {
      padding-left: 160px;
      padding-right: 0;
      padding-top: 15px;
      padding-bottom: 15px;
      background-color: #e6e6e6; }
    @include md {
      padding-left: 130px; }
    @include xs {
      padding-left: 15px; } }
  &__bottom-in {
    @include flex(space-between, center);
    @include lg {
      display: block; } } }


.logo {
  display: block;
  text-align: center;
  overflow: hidden;
  background-color: $c-accent;
  padding-top: 10px;
  color: #fff;
  @include lg {
    width: 148px; }
  @include md {
    width: 125px;
    // height: 85px
    padding-top: 5px;
    .icon {
      font-size: 35px; }
    span {
      font-size: 9px;
      padding-left: 3px;
      padding-right: 3px; } }
  span {
    font-size: 11px;
    text-transform: uppercase;
    @include thin;
    width: 100%;
    border-top: 1px solid  #15d153;
    padding: 5px;
    display: block; }
  .icon {
    fill: currentColor;
    stroke: currentColor;
    font-size: 65px;
    margin-bottom: 5px; } }

.burger {
  display: block;
  width: 28px;
  height: 28px;
  margin-top: 9px;
  margin-left: 15px;

  span:after, span:before {
    content: "";
    position: absolute;
    left: 0;
    top: 9px; }

  span:after {
    top: 18px; }

  span {
    position: relative;
    display: block; }

  span, span:after, span:before {
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px; }
  &.is-active span {
    background-color: transparent; }

  &.is-active span:before {
    transform: rotate(45deg) translate(-1px, 0px); }

  &.is-active span:after {
    transform: rotate(-45deg) translate(6px, -7px); } }

.header-controls {
  ul {
    font-size: 0; }
  li {
    display: inline-block;
    vertical-align: top;
    margin-left: 1px; }
  a {
    color: #fff;
    @include flex(center, center);
    background-color: #35383e;
    height: 45px;
    padding-left: 12px;
    padding-right: 12px;
    font-size: 14px;
    color: #fff;
    @include thin;
    text-transform: uppercase; }
  .icon {
    fill: currentColor;
    font-size: 21px; }
  .icon-heart {
    font-size: 25px; }
  &__item-search {
    @include xs {
      display: none !important; } } }
