.nav {
  a {
    text-transform: uppercase;
    color: $c-text-100;
    font-size: 16px;
    @include light;
    @include tr(color .3s);
    @include hover {
      color: #acacac; }
    @include xs {
      font-size: 15px; } }
  li {
    display: inline-block;
    @include notlast {
      margin-right: 40px; }
    @include lg {
      display: block;
      margin-bottom: 15px;
      margin-right: 0;
      @include notlast {
        margin-right: 0; } }
    @include xs {
      margin-bottom: 10px; } }
  &--footer {
    li {
      display: block;
      @include notlast {
        margin-bottom: 20px;
        @include md {
          margin-bottom: 10px; } } }
    a {
      color: #727171;
      font-size: 15px;
      text-transform: none; } } }
