.section {
  padding-top: 30px;
  padding-bottom: 40px;
  background-color: #fff;
  @include sm {
    padding-bottom: 30px; }
  &__title {
    margin-bottom: 34px;
    @include sm {
      margin-bottom: 20px; } }
  &__title-mb-sm {
    margin-bottom: 20px; }

  &--color {
    background-color: #f5f5f5; }
  &--no-pad {
    padding: 0;
    @include sm {
      padding: 0; } }
  &--border {
    border-top: 1px solid  $c-border;
    border-bottom: 1px solid  $c-border; } }

.section-text {
  max-height: 50vh;
  position: relative;
  padding-right: 40px;
  padding-top: 30px;
  @include sm {
    padding-top: 15px; }
  .h2, h2, .h3, h3 {
    margin-bottom: 5px; }
  a {
    color: $c-accent;
    text-decoration: underline; }
  p {
    margin-bottom: 30px;
    @include sm {
      margin-bottom: 20px; }
    @include xs {
      margin-bottom: 15px; } }
  ul, ol {
    padding-left: 20px;
    margin-bottom: 30px;
    @include sm {
      margin-bottom: 20px;
      padding-left: 10px; }
    @include xs {
      margin-bottom: 15px; } }
  ul {
    li {
      position: relative;
      padding-left: 15px;
      &::before {
        content: "\2022";
        font-size: 20px;
        display: inline-block;
        color: $c-accent;
        position: absolute;
        left: 0;
        top: 0; } } }
  ol {
    padding-left: 35px;
    list-style-type: decimal;
    @include sm {
      padding-left: 25px; } }
  li {
    margin-bottom: 15px;
    @include xs {
      margin-bottom: 10px; } } }


